<template>
  <div class="container">
    <div id="login" v-if="loggedin===false">
      <label for="create-data">Kérem jelentkezzen be!</label>
      <p><input type="text" id="username" v-model="username" placeholder="Felhasználónév"></p>
      <p><input type="password" v-model="pw" placeholder="Jelszó"></p>
      <p><button v-on:click="login()">Bejelentkezés</button></p>
    </div>
    <div v-if="loggedin===true">
      <h1>Utolsó bejegyzések</h1>
      <div class="create-post">
        <label for="create-post">Adja meg kisállatának adatait:</label>
        <input type="text" id="create-post" v-model="name" placeholder="Kisállat neve">
        <p>Születési dátum:</p>
        <date-pick v-model="birth"></date-pick>
        <input type="text" v-model="owner" placeholder="Tulajdonos neve">
        <input type="text" v-model="species" placeholder="Állat fajtája">
        <button v-on:click="createPost()">Hozzáadás</button>
      </div>
      <hr>
      <p class="error" v-if="error">{{error}}</p>
      <div class="posts-container">
        <div class="post" v-for="(post, index) in posts" 
        v-bind:item="post" 
        v-bind:index="index" 
        v-bind:key="post._id"
        >

          Adat rögzítve: {{`${post.createdAt.getDate()}/${post.createdAt.getMonth()}/${post.createdAt.getFullYear()}`}}
          <p class="text">Kisállat neve: {{post.name}}</p>
          <p class="date">Születési dátuma: {{post.birth}}</p>
          <p class="text">Fajtája: {{post.species}}</p>
          <p class="text">Tulajdonos neve: {{post.owner}}</p>
          <button v-on:click="deletePost(post._id)">Törlés</button>
          </div>
      </div>
      <div>
        <button v-on:click="loggedin=false">Kijelentkezés</button>
      </div>
      </div>
    </div>
</template>

<script>
import PostService from '../PostService';
import DatePick from 'vue-date-pick';
import 'vue-date-pick/dist/vueDatePick.css';

export default {
  components: {DatePick},
  name: 'PostComponent',
  data() {
    return {
      posts: [],
      username: '',
      pw: '',
      error: '',
      name: '',
      species: '',
      birth: '2010-01-01',
      owner: '',
      loggedin: false,
    }
  },
  async created() {
    try {
      this.posts = await PostService.getPosts();
    } catch(err){
      this.error = err.message;
    }
  },
  methods: {
    login() {
      if(this.username != ''  && this.pw != ''){
        if(this.username === "admin"  && this.pw === "admin"){
          this.loggedin=true;
        } else {
          alert("Hibás adatok!");
        }
      } else {
        alert("Kérem töltse ki az adatokat!");
      }
    },
    async createPost() {
      var test = false;
      if(this.name!='' && this.owner!='' && this.species!=''){
        for(var i=0; i<this.posts.length; i++){
          if(this.name === this.posts[i].name && this.owner === this.posts[i].owner && this.birth === this.posts[i].birth){
            test = true;
            alert("Az adatok már szerepelnek!");
          }
        }
        if(test===false)
        {
        await PostService.insertPost(this.name, this.birth, this.owner, this.species);
        this.posts = await PostService.getPosts();
        }
        this.name='';
        this.birth='2010-01-01';
        this.owner='';
        this.species='';
      }
      else{
        alert("Töltse ki megfelelően az adatokat!");
      }
    },
    async deletePost(id) {
      await PostService.deletePost(id);
      this.posts = await PostService.getPosts();
    }
  }
}
</script>

